.submit-info {
  background-color: $gray-300;
  padding-top: 40px;
  padding-bottom: 72px;
}

.submit-info__wrapper {
  padding-left: 20px;
  padding-right: 20px;
}

.submit-info__button {
  font-size: 14px;
  line-height: 16px;
  font-weight: 900;
  text-transform: uppercase;
  background-color: $primary-yellow;
  width: 100%;
  padding: 20px;
  border: none;
  border-radius: 0 90px 0 61px;
  margin-top: 22px;
  cursor: pointer;
}

.submit-info__button:hover {
  background-color: $black;
  color: $white;
}

.submit-info__button:active {
  background-color: $gray-600;
  color: $white;
}
