.range-filter {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 14px;
}

.range-filter__bar {
  position: relative;
  width: 100%;
  height: 10px;
  border-radius: 30px;
  background: linear-gradient(to right, $primary-yellow 26%, $gray-400 26%);
}

.range-filter__toggle {
  position: absolute;
  width: 16px;
  height: 38px;
  background-color: $white;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  top: -14px;
  left: 14px;
}

.range-filter__values-list {
  width: 100%;
  padding: 0;
  margin: 0;
  margin-top: 23px;
  list-style: none;
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  line-height: 18px;
  color: $gray-400;
}

.range-filter__values-item--current {
  color: $black;
}
